<template>
    <div class="all_com">
        <div class="title_com">
            <span>线下用户列表</span>
            <br>
            <br>
            <span style="font-size:20px">共{{this.userNum}}个</span>
        </div>
        <div class="offline_user_seach">
            <span>用户名：</span>
            <el-input v-model="input_account" placeholder="请输入内容" size="small" style="width:100px"></el-input>
            <span>邮箱：</span>
            <el-input v-model="input_email" placeholder="请输入内容" size="small" style="width:100px"></el-input>
            <span>手机：</span>
            <el-input v-model="input_mobile" placeholder="请输入内容" size="small" style="width:100px"></el-input>
            <el-button type="primary" size="mini" @click="offlineUserSeach">搜索</el-button>
        </div>
        <div class="offline_user_list">
            <el-button style="margin-bottom:20px" @click="newuserdialog = true">+新建用户</el-button>
            <el-table
                    border
                    :data="this.offlineUserList"
                    stripe
                    style="width: 100%">
                <el-table-column
                        prop="Account"
                        label="用户名">
                    <template slot-scope="scope">
                        <router-link class="account_detail" :to="{path:'/offlinemanager/offline_user_info',query:{accountId:scope.row.Id}}">
                            {{scope.row.Account}}
                        </router-link>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="Email"
                        label="邮箱">
                </el-table-column>
                <el-table-column
                        prop="PhoneNumber"
                        label="手机">
                    <template slot-scope="scope">
                        {{'+'+scope.row.PhonePrefix+'&nbsp'+scope.row.PhoneNumber}}
                    </template>
                </el-table-column>
                <el-table-column
                        prop="OrderAmount"
                        label="总订单数">
                </el-table-column>
                <el-table-column
                        prop="GoodsAmount"
                        label="总台数">
                </el-table-column>
                <el-table-column
                        label="操作">
                    <template slot-scope="scope">
                        <router-link class="account_detail" :to="{path:'/offlinemanager/offline_order_list',query:{account:scope.row.Account, accountId:scope.row.Id}}">
                            查看订单
                        </router-link>
                        |
                        <router-link class="account_detail" :to="{path:'/offlinemanager/offline_input',query:{accountId:scope.row.Id}}">
                            录入订单
                        </router-link>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-dialog
                title="新建用户"
                :visible.sync="newuserdialog"
                width="30%">
            <div>
                <span v-if="this.new_input_account == ''" style="margin-left: 60px;color:#26507B">请输入用户名</span>
                <span v-else-if="this.returnAccount.code == 0" style="margin-left: 60px;color:green">本用户名可用</span>
                <span v-else-if="this.returnAccount.code == 2 && this.new_input_account != ''" style="margin-left: 60px;color:red">用户名格式不对</span>
                <span v-else-if="this.returnAccount.code == 12001" style="margin-left: 60px;color:red">用户名已存在</span>
                <span v-else style="margin-left: 60px;color:#26507B">请输入正确格式的用户名</span>
                <br>
                <span>用户名：</span>
                <el-input v-model="new_input_account" placeholder="请输入内容" size="small" @blur="accountExist" style="width:300px"></el-input>
                <br>
                <span v-if="this.new_input_email == ''" style="margin-left: 60px;color:#26507B">请输入邮箱</span>
                <span v-else-if="this.returnEmail.code == 0" style="margin-left: 60px;color:green">本邮箱可用</span>
                <span v-else-if="this.returnEmail.code == 8" style="margin-left: 60px;color:red">邮箱格式不对</span>
                <span v-else-if="this.returnEmail.code == 12001" style="margin-left: 60px;color:red">邮箱已存在</span>
                <span v-else style="margin-left: 60px;color:red">请输入正确格式的邮箱</span>
                <br>
                <span style="display:inline-block;margin:5px 0 0 14px">邮箱：</span>
                <el-input v-model="new_input_email" placeholder="请输入内容" size="small" @blur="emailExist" style="width:300px"></el-input>

                <br>
                <span v-if="this.new_input_mobile == ''" style="margin-left: 60px;color:#26507B">请输手机号</span>
                <span v-else-if="this.returnMobile.code == 0" style="margin-left: 60px;color:green">本手机号可用</span>
                <span v-else-if="this.returnMobile.code == 2" style="margin-left: 60px;color:green">请选择区号</span>
                <span v-else-if="this.returnMobile.code == 9" style="margin-left: 60px;color:red">手机号格式不对</span>
                <span v-else-if="this.returnMobile.code == 12003" style="margin-left: 60px;color:red">手机号已存在</span>
                <span v-else style="margin-left: 60px;color:red">请输入正确格式的手机号</span>
                <br>
                <span style="display:inline-block;margin:5px 0 0 14px">手机：</span>
                <el-select v-model="area" placeholder="请选择" size="small" style="width:90px">
                    <el-option
                            v-for="item in optionsArea"
                            :key="item.id"
                            :value="item.code">
                    </el-option>
                </el-select>
                <el-input v-model="new_input_mobile" placeholder="请输入内容" size="small" @blur="mobileExist" style="width:206px"></el-input>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="newuserdialog = false">取 消</el-button>
                <el-button type="primary" @click="newUserconfirm">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import {validateEmail} from '../../fetch/validate'
    export default {
        data () {
            return {
                userNum: 0,
                input_account:'',
                input_email:'',
                input_mobile:'',
                new_input_account: '',
                new_input_email: '',
                new_input_mobile:'',
                new_email_bool: false,
                offlineUserList:[],
                newuserdialog: false,
                area:86,
                optionsArea: [],
                returnAccount:'',
                returnEmail:'',
                returnMobile:''
            }
        },
        methods: {
            getAllCountry(){
                this.$api('/admin/default/get_all_country', {}).then(res => {
                    // console.log(res)
                    this.optionsArea = res.data
            });
            },
            newUserconfirm(){
                if(this.returnAccount.code == 0 && this.returnEmail.code == 0 && this.returnMobile.code == 0){
                    this.$api('/admin/offline/add_offline_user', {
                        account:this.new_input_account,
                        email:this.new_input_email,
                        lang: 'cn',
                        phonePrefix:this.area,
                        phoneNumber:this.new_input_mobile,
                    }).then(res => {
                        // console.log(res)
                        this.getOfflineUserList()
                });
                    this.newuserdialog = false
                }

            },
            accountExist(){
                if(this.new_input_account == ''){
                    return
                }
                this.$api('/admin/offline/account_exists', {
                    account:this.new_input_account
                }).then(res => {
                    console.log(res)
                this.returnAccount = res
            });
            },
            emailExist(){
                if(this.new_input_email == ''){
                    return
                }
                // this.new_email_bool = validateEmail(this.new_input_email)
                this.$api('/admin/offline/email_exists', {
                    email:this.new_input_email
                }).then(res => {
                    // console.log(res)
                    this.returnEmail = res
            });
            },
            mobileExist(){
                if(this.new_input_mobile == ''){
                    return
                }
                this.$api('/admin/offline/phone_exists', {
                    phonePrefix: this.area,
                    phoneNumber:this.new_input_mobile
                }).then(res => {
                    console.log(res)
                this.returnMobile = res
            });
            },
            offlineUserSeach(){
                this.getOfflineUserList()
            },
            getOfflineUserList(){
                this.$api('/admin/offline/get_offline_user_list', {
                    account: this.input_account,
                    email: this.input_email,
                    phoneNumber: this.input_mobile,
                    pageNo:1,
                    pageSize: 50
                }).then(res => {
                    // console.log(res)
                let d = res.data
                this.offlineUserList = d.offlineUserList
                this.userNum = this.offlineUserList.length
            });
            }
        },
        created(){
            this.getOfflineUserList()
            this.getAllCountry()
        }
    }
</script>
<style lang='scss' scoped>
    @import "../../assets/css/titlecom.scss";
    .account_detail{
        color: #409EFF;
    }
    .account_detail:hover{
        text-decoration: underline
    }
    .offline_user_seach{
        margin: 10px 0 0 22px;
        height: 50px;
        border-bottom: 1px solid #e6e6e6
    }
    .offline_user_list{
        margin: 40px 0 0 30px
    }
</style>


